import React from 'react';
import {
    Page001
} from './index';


const components = {
    Page001: Page001
};

function Page({dimension, currentPage}) {
    const pageNumber = Intl.NumberFormat("en-US", {
        minimumIntegerDigits: 3
    }).format(currentPage);

    let PageTag = components["Page" + pageNumber];

        return(<PageTag style={{
            width: (dimension.width - (dimension.lnavwidth + dimension.rnavwidth)),
            height: dimension.height
        }} />);

}

export default Page;
